.App {
  text-align: center;
  background-color:aliceblue;
  overflow: visible;
  padding: 2vmin;
}


.App-header {
  border-color:ghostwhite;
  border-width: 0.2vmin;
  border-style: dashed dotted dashed dotted ;
  margin: 8vmax;
  margin-top: 0px;
  height: max-content;
  width: initial;
  display: flex;
  padding: 10px;
  flex-direction: row;
  font-size: calc(10px + 2vmin);
  background-color: #ffcce0;
  border-top-left-radius: 5vmin;
  border-top-right-radius: 5vmin;
  box-shadow: #444 1px 2px 5px;
}
.App-header:hover {
  background-color: #ffb3d1;
  box-shadow: #444 1px 2px 10px;
  border-style: dashed dashed  ;
  border-color:aliceblue;
  cursor: pointer;
}

.box-info{
  color:white;
  border-radius: 100%;
  font-weight: bold;
  margin: 30px;
  display: flex;
  flex-direction: column;
  align-items: stretch;
  text-shadow: black 2px 2px 5px;
}
.box-img{
  display: flex;
  height: 25vmax;
  width: 25vmax;
  align-items: center;
  justify-content: center;
  
}
.App-logo {
  position: absolute;
  height: 20vmax;
  width: 20vmax;
  box-shadow: black 2px 2px 5px;
  border-radius: 100%;
}



.App-header .box-info div div:nth-child(1){
  position: absolute;
  align-items: center;
  justify-content: center;
  background-color:#ff80b2; 
  border-radius: 100%;
  animation: App-logo-down infinite 4s linear;  
}
.App-header .box-info div div:nth-child(2){
  position: absolute;
  align-items: center;
  justify-content: center;
  background-color:#ff99c1; 
  border-radius: 100%;
  animation: App-logo-down infinite 1s linear;  
}
.App-header .box-info div:hover div:nth-child(1){
  transform: scale(1.2);
}
.App-header .box-info div:hover div:nth-child(2){
  transform: scale(1.2);
}
.App-header .box-info div:hover .App-logo {
  box-shadow: black 2px 2px 15px;
  border-radius: 100%;
  transform: scale(1.2);
  animation:  App-logo-scaleIn normal 0.6s linear;
}
.bio{
  width: 100%;
  height: 100%;
  align-self: center;
  align-items: center;
  justify-content: center;
  padding: 10px;
  font-style: italic;
  font-family:'Courier New', Courier, monospace;
  font-weight: 400;
  color:#111;

}
.icon{
  height: 100%;
  width: 100%;
}
.box-icon{
  height: 4vmax;
  width: 10vmax;
}

@keyframes App-logo-spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}
@keyframes App-logo-down {
  from {
    height: 25vmax;
    width: 25vmax;
  }
  to {
    height: 20vmax;
    width: 20vmax;  
  }
}


.App-body{
  display: flex;
  align-self: center;
  background-color:ghostwhite;
  height: 100%;
  width: 100%;
  transform: translate(0px,-10vmax);
  box-shadow:#ddd 2px 2px 10px;
  align-items: center;
  justify-content: center;
}
.App-body div{
  margin:10px;
  max-width: 70vmax;
  text-align: left;
}
